.sidebar{padding:1rem;border-radius:1rem;box-shadow:0 4px 6px rgba(0,0,0,.1)}.sidebar.dark{background-color:#1a202c}.avatarContainer{display:flex;flex-direction:column;align-items:center;margin-bottom:1.5rem}.avatarPlaceholder{width:5rem;height:5rem;background-color:var(--gray);border-radius:50%;display:flex;align-items:center;justify-content:center;cursor:pointer}.avatarPlaceholder.dark{background-color:#4a5568}.avatarPlaceholder p{color:#fff}.avatarImage{position:relative;width:100px;height:100px;border-radius:50%;margin-top:1rem;cursor:pointer}.avatarImage .uploadingOverlay{position:absolute;top:0;left:0;right:0;bottom:0;display:flex;justify-content:center;align-items:center;background-color:rgba(0,0,0,.5);color:#fff;font-size:.75rem;border-radius:50%}.avatarText{margin-top:.75rem;font-size:.875rem;color:#4a5568}.avatarText.dark{color:#edf2f7}.avatarPreview{width:5rem;height:5rem;border-radius:50%;object-fit:cover}.tab{padding:.5rem 1rem;border-radius:100px;margin-bottom:.5rem;color:var(--on-surface);cursor:pointer;transition:background-color .3s;display:flex;align-items:center}.tab.active{background-color:var(--primary);color:var(--white)}.tab.dark{color:var(--white)}.tab .icon{margin-right:5px}.tabsWrapper{display:flex;flex-direction:column}